// react:
import React, { PureComponent } from "react";

// components:

// material ui:
import { withStyles } from "@material-ui/core/styles";
import { Paper } from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";

// redux:
import { connect } from "react-redux";
import { sendMessage } from "shared/features/chat.js";

// recompose:
import { compose } from "recompose";

// jss:

const styles = (theme) => ({
	root: {
		overflowY: "auto",
		// marginBottom: "15px",
		boxShadow: "none",
		position: "absolute",
		top: 64,
		left: 0,
		bottom: 90,
		right: 0,
	},
});

class MemberList extends PureComponent {
	constructor(props) {
		super(props);

		this.messagesEnd = null;
		this.rootRef = React.createRef();
		this.shouldScroll = false;
	}

	handleClick = (event) => {};

	render() {
		const { classes } = this.props;

		return (
			<Paper className={classes.root} elevation={4}>
				aaaaaaaaaaaaaaa
			</Paper>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		messages: state.stream.chat.messages,
		accountMap: state.stream.accountMap,
		isMod: state.client.roles.mod,
		isBanned: state.client.isBanned,
		// lastMessage: state.stream.chat.messages[state.stream.chat.messages.length - 1],
	};
};

const mapDispatchToProps = (dispatch) => {
	return {
		sendMessage: (text) => {
			dispatch(sendMessage(text));
		},
	};
};

export default compose(
	withStyles(styles),
	connect(mapStateToProps, mapDispatchToProps),
)(MemberList);
